import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false
import ElTableFooter from 'el-table-footer'
import ElementUI from 'element-ui';
//注意：样式文件需要单独引入
import 'element-ui/lib/theme-chalk/index.css';
//将引入的组件注册为全局Vue组件
Vue.use(ElementUI)
Vue.use(ElTableFooter)
/* eslint-disable no-new */
Vue.component("App1",{
  template:'<div><input/><button style="color: blue">App1 Button</button>' +
      '<App2 :data1="data1" :data2="data2" :data3="data3" @click="myclick">RENDER CHILDREN????<div slot="abc">{{dataSlot}}</div></App2>'+
  '<button style="color: red" v-if="buttonshow"></button><div v-for="item in arr">{{item}}</div></div></div>',
    data(){
        return {
            arr:[],
            buttonshow:true,
            data1:"data1",
            data2:"data2",
            data3:"data3",
            dataSlot:"dataSlot"
        }
    },
    methods:{
      myclick(){
          console.log("myclick")
      }
    },
    mounted(){
        setTimeout(()=>{
           this.data2="new _ data2",
           this.data1="new _props _ data1",
           this.data3="new _ data3",
           this.arr =  ["P1","P2","P3"]
           this.buttonshow = false
            this.dataSlot = "new ——dataSlot"
            this.myclick = function () {
                console.log("addMynewClick")
            }
        },3000)
    },
    })
Vue.component("App2",{
    inject:['a'],
    props:["data1"],
    template:'<div><button>App2 Button</button><p>{{data1}}</p>Slot<slot name="abc"></slot></div>'
})
// debugger
new Vue({
  provide(){
    return{
            a:1
      }
  },
  el: '#app',
    // template:"<App1/>"
 render:h=>h(App)
})



